.articulos-blog {
  margin-bottom: 30px;
}

.info-servicio {
  width: 100%;
  border: 4px solid #1F456E;
  border-radius: 16px;
  display: flex !important;
  flex-direction: row;
  margin-bottom: 30px;
}

.div-imagen-blog {
  margin: 30px;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.div-imagen-blog img {
  max-width: 100%;
  max-height: 180px;
  border-radius: 16px;
}

.div-texto-blog {
  margin: 30px;
  width: 50%;
}
.div-texto-blog h1 {
  margin-bottom: 8px;
  width: 100%;
  background-color: #022D36;
  border-radius: 8px;
  color: #fff;
  text-transform: uppercase;
}
.div-texto-blog p {
  margin-bottom: 8px;
  text-align: justify;
}
.div-texto-blog a {
  display: flex;
  padding: 8px 16px;
  margin: 0 auto;
  float: right;
}

.centrar-boton-articulo {
  display: flex;
  text-align: center;
}